import React from 'react';
import '../css/style.css' 

import Login from './usuario/Login'
import Principal from './Principal'
import Cadastro from './usuario/Cadastro'
import CadastroSucesso from './usuario/CadastroSucesso'
import RecuperaSenha from './usuario/RecuperaSenha'

import Teste from './Teste'

import firebase from 'firebase'
import { firebaseConfig } from './utils/firebaseUtils'

import { BrowserRouter, Switch, Route } from 'react-router-dom'

class App extends React.Component {

    componentWillMount(){
        firebase.initializeApp(firebaseConfig);
    }

    render(){
        return (
            <BrowserRouter>
                <div class="base">
                    <Switch>
                        <Route path="/" exact component={Login} />
                        <Route path="/Login" exact component={Login} />
                        <Route path="/Cadastro" component={Cadastro} />
                        <Route path="/RecuperaSenha" component={RecuperaSenha} />
                        <Route path="/Principal" component={Principal} />
                        <Route path="/CadastroSucesso" component={CadastroSucesso} />

                        <Route path="/Teste" component={Teste} />                     
                    </Switch>
                </div>
            </BrowserRouter>
        );
    }
}

  

export default App;