
import React,  { Component } from 'react'
import '../css/style.css'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'
import {
    AlteraNome,
    AlteraSenha,
} from '../redux/actions'
import { Link } from 'react-router-dom'

import firebase from 'firebase'

class Teste extends Component {
     
    state = {
        newNome: 'Gab',
        inputNome: '',
    }

    inputChangeNome = event => {
        this.setState({
          inputNome: event.target.value,
        })
    }
    inputChangeSenha = event => {
        this.setState({
          inputSenha: event.target.value,
        })
    }

    salvarDados = async () => {
        const { inputNome, inputSenha } = this.state;
        const { AlteraNome, AlteraSenha } = this.props;
        
        await AlteraNome({inputNome});
        await AlteraSenha({inputSenha});
    }
    
    render(){

        return(
            <div>
               <table>
                   <tr>
                       <td>
                       <input 
                                    class="campoLogon" 
                                    value={this.state.inputNome} 
                                    onChange={this.inputChangeNome}/>
                           { this.props.newNome }<br />
                           { this.state.newNome }<br />

                           <button
                                onClick={this.salvarDados}
                           >ChangeNome</button>

                        <Link to="/Principal"> <h3>Volte ao Login </h3></Link>
                       </td>
                   </tr>
               </table>
            </div>
        );
    }
}
const mapStateToProps = state => ({   
    newNome: state.CalculosReducers.newNome,
    newSenha: state.CalculosReducers.newSenha,
})
const mapDispatchToProps = dispatch => bindActionCreators({ 
    AlteraNome,
    AlteraSenha
}, dispatch);
export default connect(mapStateToProps,    
    mapDispatchToProps
)(Teste); 